@import '../../common/styles/sass/variables.scss';
@import '../../common/styles/sass/mixins.scss';

.project{
  width: 50vh; 
  height: 50vh;
  background-color: $bgSecondColor;
  margin: 50px;
  .picture{
    height: 250px;
    width: 100%;
    background-color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    &::before{
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.5;
    }
    .showBtn{
      background-color: $primaryColor;
      padding: 10px 20px;
      border-radius: 10%;
      position: relative;
      z-index: 1;
      color: $textColor;
    } 
  }
  .projectInfo{
    padding: 25px 20px;
    .projectName{
      letter-spacing: 1px;
      margin: 0 0 5px;
      color: $textColor;
      @include text($titleFontFamily, 16px, 1.2 );
    }
    .projectDes{
      word-break: break-all;
      text-align: start;
      font-weight: 400;
      color: $textColor;
      display: flex;
      align-items: flex-start;
      @include text($textFontFamily ,12px, 1.8);
      
    }
  }
  
}



 
